* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
}
@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  margin: 0 auto;
  max-width: 1100px;
}
.body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 17px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
}
.body--scrolloff {
  overflow: hidden;
}
.header {
  width: 100%;
}
.header__wrap {
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  padding: 10px 20px;
  border-bottom: 1px solid #000;
  background: rgba(255, 255, 255, 0.2);
}
.header__inners {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__title {
  font-family: "PT Sans", "Arial", sans-serif;
  color: #ffffff;
  font-size: 32px;
  font-weight: bold;
}
.header__subtitle {
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: normal;
  font-size: 22px;
  color: #ffffff;
}
.main {
  width: 100%;
  height: 100%;
  background-image: url('../../img/main__bg.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.footer {
  width: 100%;
  height: 90px;
}
.clock {
  display: grid;
  grid-template-columns: 1fr min-content 1fr;
  grid-column-gap: 8px;
}
.clock__text {
  font-size: 22px;
  color: #ffffff;
}
.clock__hourse {
  text-align: right;
}
.clock__minutes {
  text-align: left;
}
